.zolo-post-comments-wrap {
    display: grid;
    grid-template-columns: repeat(1, 1fr);
    grid-gap: 50px;

    .zolo-meta {
        margin-bottom: 15px;
        display: flex;
        align-items: center;
    }

    .zolo-meta .zolo-avatar {
        margin-right: 15px;
    }

    .zolo-meta .zolo-avatar img {
        --zolo-avatar-size: 80px;
        border-radius: 50%;
        width: var(--zolo-avatar-size);
        height: var(--zolo-avatar-size);
        max-width: unset;
        vertical-align: middle;
    }

    .zolo-meta .zolo-author-name {
        color: #2b2d42;
        font-size: 18px;
        font-weight: 500;
        text-transform: capitalize;
        transition: color 0.3s ease;
        overflow: hidden;
        text-overflow: ellipsis;
        display: -webkit-box;
        -webkit-line-clamp: 1;
        -webkit-box-orient: vertical;
        text-decoration: none;
        margin: 0 0 7px;
    }

    .zolo-meta .zolo-author-name:hover {
        color: #ef233c;
    }

    .zolo-meta .zolo-date,
    .zolo-text {
        color: #798184;
        font-size: 14px;
    }

    .zolo-text {
        margin: 0;
        line-height: 1.6;
    }
}
